@import url(https://fonts.googleapis.com/css2?family=Inknut+Antiqua:wght@400;900&display=swap);
@tailwind base;
@tailwind components;
@tailwind utilities;

@font-face {
  font-family: 'AvQest';
  src: url(https://josefbud.github.io/d2-runewords/static/media/AvQest.ttf.ef83ab9e.woff) format('woff');
}

:root {
  --main-primary: #92140c;
  --main-primary-light: #c94a34;
  --main-primary-dark: #5e0000;
  --main-secondary: #4d5055;
  --main-secondary-light: #797c81;
  --main-secondary-dark: #25282c;
}

body {
  background: #5e0000;
  background: var(--main-primary-dark);
  font-family: 'AvQest', serif;
  color: #fff;
}

.bg-secondary-light {
  background: #797c81;
  background: var(--main-secondary-light);
}


